import React from 'react';

import { FaEye } from "react-icons/fa";
import { FaGithub } from "react-icons/fa";

import './mywork-item.styles.scss';

const MyworkItem = ({ imageUrl, tags, demoUrl, githubUrl, description }) => (
    <div className='work-item'> 
        <div className='work-item__content'>
            <div className='img-box'>
                <img className='image' alt='robots' src={imageUrl} />
            </div>
            <div className='tags-box'>
                <span className='tags'># {tags}</span>
            </div>
            <div className='btn-box'>
                <a className='button_1' href={demoUrl} target='_blank' rel="noopener noreferrer" ><span className='icon'><FaEye /></span>Live demo</a>
                <a className='button_2' href={githubUrl} target='_blank' rel="noopener noreferrer" ><span className='icon'><FaGithub /></span>Source code</a>
            </div>
        </div>
        <div className='work-item__footer'>
            <p className='work-item__footer--text'>{description}</p>
        </div>
    </div> 
)

export default MyworkItem;

