import React from "react";

import MyworkItem from "../../components/mywork-item/mywork-item.component";

import "./mywork.styles.scss";
import { WORK_DATA } from "./WORK_DATA";

class MyworkPage extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      workItems: WORK_DATA,
    };
  }

  componentDidMount() {
    window.scrollTo(0, 0);
  }

  render() {
    return (
      <div className="mywork">
        <div className="mywork__header">
          <h1 className="mywork__header--title">My Work</h1>
          <p className="mywork__header--subtitle">
            Check out some of my projects...
          </p>
        </div>
        <div className="mywork__content">
          {this.state.workItems.map(({ id, ...otherSectionProps }) => (
            <MyworkItem key={id} {...otherSectionProps} />
          ))}
        </div>
        <p className="mywork__note">
          * Note: You might experience a few seconds delay for the first time
          you open the application hosted by Heroku. It has nothing to do with
          the performance optimization of the application itself.
        </p>
      </div>
    );
  }
}

export default MyworkPage;
